define('dummy/tests/unit/validators/base-test', ['ember', 'qunit', 'ember-validations/validators/base'], function (Ember, qunit, Base) {

  'use strict';

  var model, Model, options, CustomValidator, validator;
  var get = Ember['default'].get;
  var run = Ember['default'].run;

  qunit.module("Base Validator", {
    setup: function setup() {
      Model = Ember['default'].Object.extend({
        dependentValidationKeys: {}
      });
      CustomValidator = Base['default'].extend({
        init: function init() {
          this._super();
          this.dependentValidationKeys.pushObject("otherAttribute");
        },
        call: function call() {}
      });
      run(function () {
        model = Model.create();
      });
    }
  });

  qunit.test("when value is not empty", function (assert) {
    run(function () {
      validator = CustomValidator.create({ model: model, property: "attribute" });
    });
    assert.equal(get(validator, "isValid"), true);
  });

  qunit.test("validator has isInvalid flag", function (assert) {
    run(function () {
      validator = CustomValidator.create({ model: model, property: "attribute" });
    });
    assert.equal(get(validator, "isInvalid"), false);
  });

  qunit.test("generates dependentValidationKeys on the model", function (assert) {
    run(function () {
      validator = CustomValidator.create({ model: model, property: "attribute" });
    });
    assert.deepEqual(get(model, "dependentValidationKeys"), { attribute: ["otherAttribute"] });
  });

  qunit.test("inactive validators should be considered valid", function (assert) {
    var canValidate = true;
    run(function () {
      validator = CustomValidator.createWithMixins({
        model: model,
        property: "attribute",
        canValidate: (function (_canValidate) {
          var _canValidateWrapper = function canValidate() {
            return _canValidate.apply(this, arguments);
          };

          _canValidateWrapper.toString = function () {
            return _canValidate.toString();
          };

          return _canValidateWrapper;
        })(function () {
          return canValidate;
        }),
        call: function call() {
          this.errors.pushObject("nope");
        }
      });
    });
    assert.equal(get(validator, "isValid"), false);
    canValidate = false;
    run(validator, "validate");
    assert.equal(get(validator, "isValid"), true);
  });

});