define('dummy/tests/unit/validators/local/confirmation-test', ['ember', 'qunit', 'ember-validations/validators/local/confirmation', 'ember-validations/mixin', 'dummy/tests/helpers/container'], function (Ember, qunit, Confirmation, Mixin, container) {

  'use strict';

  var model, Model, options, validator;
  var get = Ember['default'].get;
  var set = Ember['default'].set;
  var run = Ember['default'].run;

  qunit.module("Confirmation Validator", {
    setup: function setup() {
      Model = Ember['default'].Object.extend(Mixin['default'], {
        container: container.buildContainer()
      });
      run(function () {
        model = Model.create();
      });
    }
  });

  qunit.test("when values match", function (assert) {
    options = { message: "failed validation" };
    run(function () {
      validator = Confirmation['default'].create({ model: model, property: "attribute", options: options });
      set(model, "attribute", "test");
      set(model, "attributeConfirmation", "test");
    });
    assert.deepEqual(validator.errors, []);
    run(function () {
      set(model, "attributeConfirmation", "newTest");
    });
    assert.deepEqual(validator.errors, ["failed validation"]);
    run(function () {
      set(model, "attribute", "newTest");
    });
    assert.deepEqual(validator.errors, []);
  });

  qunit.test("when values do not match", function (assert) {
    options = { message: "failed validation" };
    run(function () {
      validator = Confirmation['default'].create({ model: model, property: "attribute", options: options });
      set(model, "attribute", "test");
    });
    assert.deepEqual(validator.errors, ["failed validation"]);
  });

  qunit.test("when original is null", function (assert) {
    run(function () {
      validator = Confirmation['default'].create({ model: model, property: "attribute" });
      model.set("attribute", null);
    });
    assert.ok(Ember['default'].isEmpty(validator.errors));
  });

  qunit.test("when confirmation is null", function (assert) {
    run(function () {
      validator = Confirmation['default'].create({ model: model, property: "attribute" });
      model.set("attributeConfirmation", null);
    });
    assert.ok(Ember['default'].isEmpty(validator.errors));
  });

  qunit.test("when options is true", function (assert) {
    options = true;
    run(function () {
      validator = Confirmation['default'].create({ model: model, property: "attribute", options: options });
      set(model, "attribute", "test");
    });
    assert.deepEqual(validator.errors, ["doesn't match attribute"]);
  });

  qunit.test("message integration on model, prints message on Confirmation property", function (assert) {
    var otherModel,
        OtherModel = Model.extend({
      validations: {
        attribute: {
          confirmation: true
        }
      }
    });

    run(function () {
      otherModel = OtherModel.create();
      set(otherModel, "attribute", "test");
    });

    assert.deepEqual(get(otherModel, "errors.attributeConfirmation"), ["doesn't match attribute"]);
    assert.deepEqual(get(otherModel, "errors.attribute"), []);
  });

});