define('dummy/tests/helpers/container', ['exports', 'ember', 'ember/resolver'], function (exports, Ember, Resolver) {

  'use strict';

  exports.buildContainer = buildContainer;

  function buildContainer() {
    var container = new Ember['default'].Container();
    container.resolver = buildResolver();
    container.normalizeFullName = container.resolver.normalize;
    container.describe = container.resolver.describe;
    container.makeToString = container.resolver.makeToString;

    return container;
  }

  function buildResolver() {
    var resolver = Resolver['default'].create({
      namespace: Ember['default'].Namespace.create({
        modulePrefix: "dummy"
      })
    });

    function resolve(fullName) {
      return resolver.resolve(fullName);
    }

    resolve.describe = function (fullName) {
      return resolver.lookupDescription(fullName);
    };

    resolve.makeToString = function (factory, fullName) {
      return resolver.makeToString(factory, fullName);
    };

    resolve.normalize = function (fullName) {
      if (resolver.normalize) {
        return resolver.normalize(fullName);
      } else {
        Ember['default'].deprecate("The Resolver should now provide a 'normalize' function", false);
        return fullName;
      }
    };

    resolve.__resolver__ = resolver;

    return resolve;
  }

});